import { Badge, Col, Container, Row } from 'react-bootstrap';

import './Home.css';

const metadata = require('./Article/.metadata.json');

function Home() {
    return (
        <>
            <Container>
                {/* Banner */}
                <div className='p-4 mb-4 text-white rounded bg-dark home-banner'>
                    <Col md={6} className='px-0'>
                        <h1 className='display-4 fst-italic'>Hello, World! This is 91d906h4's Blog.</h1>
                        <p className='lead my-3'>Hi! Welcome to my blog on GitHub page. This blog records my articles and works. Hope you a great day!</p>
                        <p className='lead mb-0'><a href='about/' className='text-white fw-bold'>About...</a></p>
                    </Col>
                </div>

                {/* Article */}
                <Row>
                    <Col md={8}>
                        <>
                            {
                                Object.keys(metadata).slice(-10).map((id: string) => {
                                    try {
                                        var article = metadata[id];
                                        return (
                                            <div className='position-relative' key={Math.random()}>
                                                <Row>
                                                    <Col xs={3} className='text-center'>
                                                        <img src={(article.cover === '' || article.cover === undefined ? '../assets/defualt-image.png' : article.cover)} onError={(e: any) => e.target.src = '../assets/defualt-image.png'} className='home-image-view' />
                                                    </Col>
                                                    <Col xs={9}>
                                                        <h2 className='home-article-title'>{article.title}</h2>
                                                        <p>{article.time} by <span className='text-primary'>{article.author}</span></p>
                                                        <div className='home-article-description'>
                                                            <span>{article.description}</span><br />
                                                            <span>
                                                                {
                                                                    article.tag.map((tag: string) => {
                                                                        return <span key={Math.random()}> <Badge bg="secondary">{tag}</Badge></span>;
                                                                    })
                                                                }
                                                            </span>
                                                        </div>
                                                        <a href={'/article/' + id} className='stretched-link'></a>
                                                    </Col>
                                                </Row>
                                                <hr />
                                            </div>
                                        );
                                    }
                                    catch (error) {console.log(error)}
                                })
                            }
                        </>
                    </Col>
                    <Col md={4}>
                        <div className='position-sticky d-none d-sm-block'>
                            <div className='p-4 mb-3 bg-light rounded'>
                                <h4 className='fst-italic home-article-title'>About</h4>
                                <p className='mb-0'>This is my personal blog. It records my recent works and some ideas. If you want, you can find my contact details on about page.</p>
                            </div>
                        </div>
                    </Col>
                </Row>
            </Container>
        </>
    );
}

export default Home;